import { Routes } from '@angular/router';

const Routing: Routes = [
  {
    path: '',
    redirectTo: '/dashboard',
    pathMatch: 'full',
  },
  {
    path: 'dashboard',
    loadChildren: () =>
      import('./dashboard/dashboard.module').then((m) => m.DashboardModule),
  },
  {
    path: 'admin',
    loadChildren: () =>
      import('./admin/admin.module').then((m) => m.InternalTeamModule),
  },
  {
    path: 'supplier',
    loadChildren: () =>
      import('./supplier/supplier.module').then((m) => m.SupplierModule),
  },
  {
    path: 'customer',
    loadChildren: () =>
      import('./customer/customer.module').then((m) => m.CustomerModule),
  },
  {
    path: 'customer-payment',
    loadChildren: () =>
      import('./customer-payment/customer-payment.module').then(
        (m) => m.PaymentModule
      ),
  },
  {
    path: 'supplier-payment',
    loadChildren: () =>
      import('./supplier-payment/supplier-payment.module').then(
        (m) => m.SupplierPaymentModule
      ),
  },
  {
    path: 'processing-payment',
    loadChildren: () =>
      import('./processing-payment/processing-payment.module').then(
        (m) => m.ProcessingPaymentModule
      ),
  },
  {
    path: 'refund',
    loadChildren: () =>
      import('./refund/refund.module').then((m) => m.RefundModule),
  },
  {
    path: 'office-expenses',
    loadChildren: () =>
      import('./office-expenses/office-expenses.module').then(
        (m) => m.OfficeExpensesModule
      ),
  },
  {
    path: 'report',
    loadChildren: () =>
      import('./report/report.module').then((m) => m.ReportModule),
  },
  {
    path: 'user',
    loadChildren: () => import('./user/user.module').then((m) => m.UserModule),
  },
  {
    path: '**',
    redirectTo: 'error/404',
  },
];

export { Routing };
